import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from '../_guards';
import { JacketOptionsComponent } from './jacket-options/jacket-options.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'jacket-options',
    pathMatch: 'full',
  },
  {
    path: 'jacket-options',
    component: JacketOptionsComponent,
    canActivate: [AuthGuard],
    data: { title: 'Jacket Options', type: 'jacket-options' },
  },
];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class CustomizationRoutingModule {}
